export default class FormValidator {
  
  constructor(config, formElement) {
    this._config = config;
    this._formElement = formElement;

    // ÑÑÐ°Ð·Ñ Ð¿Ð¾Ð»ÑÑÐ¸Ð¼ Ð²ÑÐµ Ð¸Ð¼Ð¿ÑÑÑ
    this._inputList = Array.from(this._formElement.querySelectorAll(this._config.inputSelector));
    // Ð¿Ð¾Ð»ÑÑÐ¸Ð¼ ÐºÐ½Ð¾Ð¿ÐºÑ
    this._buttonElement = this._formElement.querySelector(this._config.submitButtonSelector)
  }


  // Ð¿Ð¾ÐºÐ°Ð¶ÐµÐ¼ Ð¾ÑÐ¸Ð±ÐºÑ
  _showInputError(inputElement) {
    const errorElement = this._formElement.querySelector(`#${inputElement.id}-error`);
    inputElement.classList.add(this._config.inputErrorClass);
    errorElement.textContent = inputElement.validationMessage;
    errorElement.classList.add(this._config.errorClass);
  };

  // ÑÐ¿ÑÑÑÐµÐ¼ Ð¾ÑÐ¸Ð±ÐºÑ
  _hideInputError(inputElement) {
    const errorElement = this._formElement.querySelector(`#${inputElement.id}-error`);
    inputElement.classList.remove(this._config.inputErrorClass);
    errorElement.classList.remove(this._config.errorClass);
    errorElement.textContent = '';
  };

  _checkInputValidity(inputElement) {
    // Ð²Ð°Ð»Ð¸Ð´Ð¸ÑÑÐµÐ¼ Ð²Ð²ÐµÐ´ÐµÐ½Ð½ÑÐµ Ð´Ð°Ð½Ð½ÑÐµ Ð¸ Ð¿Ð¾Ð´ÑÐºÐ°Ð·ÑÐ²Ð°ÐµÐ¼ ÑÑÐ¾ Ð½Ðµ ÑÐ°Ðº
    if (!inputElement.validity.valid) {
      this._showInputError(inputElement);
    } else {
      this._hideInputError(inputElement);
    }
  }

  // Ð´Ð¾ÑÑÑÐ¿Ð½Ð¾ÑÑÑ ÐºÐ½Ð¾Ð¿ÐºÐ¸ ÑÐ¾ÑÑÐ°Ð½Ð¸ÑÑ
  _toggleButtonState () {
    if (!this._formElement.checkValidity()) {
      this._buttonElement.classList.add(this._config.inactiveButtonClass);
      this._buttonElement.disabled = true;
    } else {
      this._buttonElement.classList.remove(this._config.inactiveButtonClass);
      this._buttonElement.disabled = false;
    }
  };

  _setEventListeners() {
    // Ð½Ð° Ð²ÑÐµ Ð¸Ð¼Ð¿ÑÑÑ Ð´Ð¾Ð±Ð°Ð²Ð¸Ð¼ ÑÐµÐ°ÐºÑÐ¸Ñ Ð½Ð° Ð²Ð²Ð¾Ð´
    this._inputList.forEach((inputElement) => {
      inputElement.addEventListener('input', () => {
        // Ð¿ÑÐ¾Ð²Ð°Ð»Ð¸Ð´Ð¸ÑÑÐµÐ¼
        this._checkInputValidity(inputElement);
        // Ð¿ÑÐ¾Ð²ÐµÑÐ¸Ð¼ Ð´Ð¾ÑÑÑÐ¿Ð½Ð¾ÑÑÑ ÐºÐ½Ð¾Ð¿ÐºÐ¸
        this._toggleButtonState();
      });
    });
  }

  // Ð°ÐºÑÐ¸Ð²Ð°ÑÐ¸Ñ Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸
  enableValidation() {
    //ÑÑÑÐ°Ð½Ð¾Ð²Ð¸Ð¼ ÑÐ»ÑÑÐ°ÑÐµÐ»ÐµÐ¹
    this._setEventListeners();
  }

  //Ð²Ð°Ð»Ð¸Ð´Ð¸ÑÑÐµÐ¼ ÑÐ¾ÑÐ¼Ñ ÑÑÐ°Ð·Ñ Ð¿Ð¾ÑÐ»Ðµ Ð¿Ð¾Ð´Ð½ÑÑÐ¸Ñ
  validateAfterOpen() {
    // Ð¸Ð´ÐµÐ¼ Ð¿Ð¾ Ð²ÑÐµÐ¼ input Ð¸ Ð¿ÑÑÑÐµÐ¼ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½ÑÐµ Ð¾ÑÐ¸Ð±ÐºÐ¸, ÐµÑÐ»Ð¸ Ð±ÑÐ»Ð¸
    this._inputList.forEach((inputElement) => {
      this._hideInputError(inputElement);
    });
    // Ð¿ÑÐ¾Ð²ÐµÑÑÐµÐ¼ Ð´Ð¾ÑÑÑÐ¿Ð½Ð¾ÑÑÑ ÐºÐ½Ð¾Ð¿ÐºÐ¸
    this._toggleButtonState();
  }

}


